











import Vue from 'vue';
import Component from 'vue-class-component';
import Header from '../components/Header.vue';
import { Prop } from 'vue-property-decorator';
import { NOTIFY_KEY } from '@/constant';
import { getIp } from '@/assets/utils/util';

@Component({
    components: {
        'app-nav': Header
    }
})
export default class Main extends Vue {
    private timer?: number;

    public created() {
        this.link();
    }

    public destroyed() {
        if (this.timer) {
            clearTimeout(this.timer);
        }
    }

    private link() {
        const ip = getIp();
        if (!ip) {
            return;
        }

        const ws = new WebSocket(`ws://${ip}/realtime/alarm`);
        ws.onmessage = (e: MessageEvent) => {
            let data: Omit<IAlarm, 'id'>;
            try {
                data = JSON.parse(e.data);
            } catch (e) {
                const arr = e.data.split('$');
                data = {
                    baseNo: arr[0],
                    tagNo: arr[1],
                    type: +arr[2],
                    alarmTime: +arr[3],
                    alarmMsg: arr[4]
                };
            }
            if (Date.now() - data.alarmTime <= 1000) {
                this.$event.emit(NOTIFY_KEY, data);
            }
        };
    }
}
